.container-form, .container-wallet table {
  background: #FFFFFF;
  border: 1px solid #D4DFEF;
  box-sizing: border-box;
  border-radius: 10px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  margin: 0 10%;
  padding: 0 20px;
  width: 80%;
}

.title-form, .title-table {
  margin: 20px 0 10px 11%;
  color: #172635;
  font-weight: 500;
  font-size: 18px;
}

.container-wallet table {
  align-items: flex-start;
  font-size: 12px;
  text-align: center;
  height: 60vh;
  padding: 20px;
  color: #172635;
}

th {
  padding: 10px 20px;
}

td {
  font-size: 11px;
  color: #5D87A8;
}

tr {
  font-size: 12px;
  color: #67E799;
}

.button-cell {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

td button {
  background: transparent;
  border: none;
  cursor: pointer;
  width: 20px;
  height: 20px;
  align-self: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* teste */

.button-form {
  background: linear-gradient(269.11deg, #67E799 0.19%, #4AD07E 100%);
  border-radius: 5px;
  border: none;
  width: 140px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  margin: 0 0 0 20px;
  cursor: pointer;
}

.disabled {
  background: #E5E5E5;
  cursor: auto;
}

.button-form p {
  font-size: 12px;
  color: #FFFFFF;
}

.button-form img {
  width: 10px;
}

.container-header {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: row;
  margin-top: 20px;
}

.header-logo-box, .header-info-box {
  display: flex;
  width: 40%;
}

.header-info-box {
  align-self: center;
  display: flex;
  justify-content: flex-end;
}

.header-title-logo {
  font-size: 24px;
  color: #4CCC81;
}

.header-wallet-logo {
  align-self: center;
  height: 40px;
}

.header-info-box span {
  color: #5D87A8;
  font-weight: 500;
  font-size: 14px;
}

.total {
  margin: 0 5px;
}

.profile-email {
  margin: 0 40px 0 0;
}

.input-label {
  display: flex;
  color: #5D87A8;
  font-weight: 500;
  font-size: 12px;
  align-items: center;
}

.input-form {
  width: 120px;
  height: 30px;
  background: #E7F5ED;
  border: none;
  border-radius: 5px;
  margin: 0 10px 0 3px;
  color: #4CCC81;
  font-size: 10px;
  padding: 0 3px;
}

.value {
  width: 40px;
}

.select-label {
  display: flex;
  color: #5D87A8;
  font-weight: 500;
  font-size: 12px;
  align-items: center;
}

.select-form {
  width: 120px;
  height: 30px;
  background: #E7F5ED;
  border: none;
  border-radius: 5px;
  margin: 0 15px 0 3px;
  color: #4CCC81;
  font-size: 10px;
  text-align: center;
}

.currency {
  width: 50px;
}

.tag {
  width: 90px;
}

.select-form:nth-child(2) {
  width: 100px;
}