.container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.login-image {
  background-color: transparent;
  width: 500px;
  margin: 20px;
}

.login-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 600px;
  margin: 20px;
}

.logo-box {
  display: flex;
  margin: 20px;
}

.title-logo {
  color: #4CCC81;
  font-size: 42px;
  margin: 20px;
}

.wallet-logo {
  height: 65px;
  align-self: center;
}

.label {
  display: flex;
  flex-direction: column;
  color: #5D87A8;
  margin: 5px;
}

.input {
  width: 300px;
  height: 45px;
  background: #FFFFFF;
  border: 1px solid #D4DFEF;
  box-sizing: border-box;
  border-radius: 10px;
  color: #5D87A8;
  font-size: 18px;
  padding: 0px 12px;
}

.login-button {
  background: linear-gradient(269.11deg, #67E799 0.19%, #4AD07E 100%);
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.25);
  border: none;
  border-radius: 10px;
  color: white;
  width: 300px;
  height: 45px;
  font-size: 18px;
  text-align: start;
  padding: 0px 12px;
  margin: 20px;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.arrow {
  opacity: 0;
  transform: translateX(-50px);
  transition: 0.5s;
}

.login-button:hover .arrow {
  opacity: 1;
  transform: translateX(0);
}

.disabled {
  background: #E5E5E5;
}